[class*="text-icon"] {
    &::before {
        @extend .svg-sprite;
        content: "";
        display: block;
        margin: 0.1em auto;
    }
}


@include mq($from: tablet) {
    [class*="text-icon"] {
        &::before {
            display: inline-block;
            font-size: 0.5em;
            margin-bottom: -0.65em;
        }
    }
}

// Specific instances extend styles generated by SVG sprite task.
.text-icon--mouse::before {
    @extend .svg-icon-mouse;
}

.text-icon--phone::before {
    @extend .svg-icon-phone;
}

.text-icon--phone-dark::before {
    @extend .svg-icon-phone--dark;
}
